.navbar {
  background-color: #ff69b4; /* bright pink for fun */
  padding: 15px 0;
  display: flex;
  justify-content: center;
  gap: 30px;
  font-size: 1.2rem;
  font-weight: bold;
}

.navbar a {
  color: white;
  text-decoration: none;
  transition: transform 0.2s, color 0.2s;
}

.navbar a:hover {
  color: #ffff00; /* yellow on hover */
  transform: scale(1.2); /* enlarge slightly */
}
