.home {
  text-align: center;
  margin-top: 50px;
}

.ai-btn {
  padding: 15px 30px;
  font-size: 1.2rem;
  background-color: #ff4500; /* fun orange */
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.2s, background-color 0.2s;
}

.ai-btn:hover {
  background-color: #32cd32; /* green on hover */
  transform: scale(1.1);
}

.thought-box {
  margin-top: 20px;
  padding: 20px;
  background-color: #ffff99; /* light yellow */
  border-radius: 10px;
  font-size: 1.2rem;
  font-style: italic;
}
